import axios from 'axios'
import { useSnackbar } from 'notistack'
import { useEffect, useState } from 'react'
import { useAuthHeader } from 'react-auth-kit'
import { Helmet } from 'react-helmet-async'
import { useSearchParams } from 'react-router-dom'

import {
  CircularProgress,
  Container,
  FormControl,
  Grid,
  InputLabel,
  MenuItem,
  Pagination,
  Select,
  type SelectChangeEvent,
  Stack,
  Typography,
  useMediaQuery,
  useTheme
} from '@mui/material'

import orders from 'src/data/SortingOrders'
import CLUB_BELONGING from 'src/data/belonging'
import CLUB_CATEGORIES, { type ClubCategories } from 'src/data/categories'
import { CLUB_SEARCH_MESSAGE } from 'src/data/messages'

import ClubList from 'src/components/ClubList'
import { type ClubPreviewProps } from 'src/components/ClubPreview'
import PageTitleWrapper from 'src/components/PageTitleWrapper'
import Searchbox from 'src/components/Searchbox'

interface SearchAPIResponse {
  clubs: ClubPreviewProps[]
  numPages: number
}

const SELECT_COMPONENT_WIDTH = '160px'

const Search: React.FC = () => {
  const theme = useTheme()
  const isNotMobile = useMediaQuery(theme.breakpoints.up('sm'))
  const [searchParams, setSearchParams] = useSearchParams()
  const [params, setParams] = useState({
    name: searchParams.get('name') ?? '',
    category: (searchParams.get('category') as ClubCategories | '') ?? '',
    belonging: searchParams.get('belonging') ?? '',
    page: searchParams.has('page') ? parseInt(searchParams.get('page') || '1') : null,
    order: searchParams.has('order') ? searchParams.get('order') : ''
  })

  const [clubs, setClubs] = useState<ClubPreviewProps[]>([])
  const [numPages, setNumPages] = useState(1)
  const [isLoading, setIsLoading] = useState(false)
  const { enqueueSnackbar } = useSnackbar()

  const handleSubmit: React.FormEventHandler<HTMLFormElement> = event => {
    event.preventDefault()
    const search = document.getElementById('search') as HTMLInputElement
    setParams(prev => ({ ...prev, name: search.value, page: null, order: '' }))
  }

  const handleChange = ({
    target: { name, value }
  }: React.ChangeEvent<HTMLInputElement> | SelectChangeEvent<string>) => {
    setParams(prev => ({ ...prev, belonging: name === 'belonging' ? '' : prev.belonging, page: null, [name]: value }))
  }

  const handleClickCategory: React.MouseEventHandler<HTMLButtonElement> = event => {
    const category = event.currentTarget.getAttribute('data-value') as ClubCategories

    if (!CLUB_CATEGORIES.includes(category)) {
      enqueueSnackbar(CLUB_SEARCH_MESSAGE.WRONG_CATEGORY, { variant: 'warning' })
      return
    }
    setParams(prev => ({ ...prev, category: category }))
  }

  const handlePageChange = (event: React.ChangeEvent<HTMLInputElement | unknown>, value: number) =>
    setParams(prev => ({ ...prev, page: value === 1 ? null : value }))

  useEffect(() => {
    const newParams = Object.assign(
      {},
      params.name ? { name: params.name } : null,
      params.category ? { category: params.category } : null,
      params.belonging ? { belonging: params.belonging } : null,
      params.page ? { page: params.page } : null,
      params.order ? { order: params.order } : null
    )
    setSearchParams(newParams)
  }, [params])

  const authHeader = useAuthHeader()

  useEffect(() => {
    setIsLoading(true)
    const apiParams = Object.assign(
      {},
      params.name ? { name: params.name } : null,
      params.category ? { category: params.category } : null,
      params.belonging ? { belonging: params.belonging } : null,
      params.page ? { page: params.page } : null,
      { count: 12 },
      params.order ? { order: params.order } : null
    )
    axios
      .get<SearchAPIResponse>(`${process.env.REACT_APP_APIHOST}/search/clubs`, {
        params: apiParams,
        headers: { Authorization: authHeader() }
      })
      .then(res => {
        setNumPages(res.data?.numPages)
        setClubs(res.data?.clubs ?? [])
        setIsLoading(false)
      })
      .catch(err => {
        enqueueSnackbar(err.response.data?.message ?? '서버에서 오류가 발생한 것 같아요. 다시 시도해주세요', {
          variant: 'error'
        })
        setIsLoading(false)
      })
  }, [searchParams])

  return (
    <>
      <Helmet>
        <title>검색 결과</title>
      </Helmet>
      <PageTitleWrapper>
        <Grid container alignItems="center">
          <Grid item>
            <Stack direction="row" alignItems="baseline">
              <Typography variant="h3" component="h3" gutterBottom sx={{ marginRight: 1 }}>
                {params.belonging === '' ? '검색 결과' : params.belonging}
              </Typography>
              <Typography variant="inherit" gutterBottom>
                {params.category}
              </Typography>
            </Stack>
            <Typography variant="subtitle2">키워드와 카테고리, 소속을 활용해 원하는 동아리를 찾아보세요</Typography>
          </Grid>
        </Grid>
        <Searchbox
          handleSubmit={handleSubmit}
          searchDefaultValue={params.name}
          filterCategory={params.category}
          handleClickCategory={handleClickCategory}
        />
      </PageTitleWrapper>
      <Container
        sx={{ display: 'flex', flexDirection: 'column', justifyContent: 'space-between', alignItems: 'center' }}
      >
        <Stack
          width="100%"
          direction={isNotMobile ? 'row' : 'column'}
          spacing={1}
          justifyContent={'flex-end'}
          sx={{ marginBottom: 3 }}
        >
          {/* {isDepartmentClubSearchPage ? (
            <FormControl size="small" fullWidth={!isNotMobile}>
              <InputLabel id="department-label">단과대</InputLabel>
              <Select
                labelId="department-label"
                id="department"
                name="category"
                label="단과대"
                value={params.category}
                onChange={handleChange}
                sx={{ minWidth: SELECT_COMPONENT_WIDTH }}
              >
                <MenuItem value="">선택하지 않음</MenuItem>
                {Object.keys(departments)?.map(department => (
                  <MenuItem key={department} value={department}>
                    {department}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          ) : (
            <FormControl size="small" fullWidth={!isNotMobile}>
              <InputLabel id="category-label">카테고리</InputLabel>
              <Select
                labelId="category-label"
                id="category"
                name="category"
                label="카테고리"
                value={params.category}
                onChange={handleChange}
                sx={{ minWidth: SELECT_COMPONENT_WIDTH }}
              >
                <MenuItem value="">선택하지 않음</MenuItem>
                {CLUB_CATEGORIES &&
                  Object.entries(CLUB_CATEGORIES).map(([key, name]) => (
                    <MenuItem key={key} value={key}>
                      {name}
                    </MenuItem>
                  ))}
              </Select>
            </FormControl>
          )} */}

          <FormControl size="small" fullWidth={!isNotMobile}>
            <InputLabel id="belonging-label">소속</InputLabel>
            <Select
              labelId="belonging-label"
              id="belonging"
              name="belonging"
              label="소속"
              value={params.belonging}
              onChange={handleChange}
              sx={{ minWidth: SELECT_COMPONENT_WIDTH }}
            >
              <MenuItem value="">선택하지 않음</MenuItem>
              {CLUB_BELONGING &&
                CLUB_BELONGING.map((name, idx) => (
                  <MenuItem key={idx} value={name}>
                    {name}
                  </MenuItem>
                ))}
            </Select>
          </FormControl>
          <FormControl size="small" fullWidth={!isNotMobile}>
            <InputLabel id="order-label">정렬</InputLabel>
            <Select
              labelId="order-label"
              id="order"
              name="order"
              label="정렬"
              value={params.order ?? ''}
              onChange={handleChange}
              sx={{ minWidth: SELECT_COMPONENT_WIDTH }}
            >
              <MenuItem value="">선택하지 않음</MenuItem>
              {orders &&
                Object.entries(orders).map(([key, value]) => (
                  <MenuItem key={key} value={key}>
                    {value}
                  </MenuItem>
                ))}
            </Select>
          </FormControl>
        </Stack>
        {isLoading ? <CircularProgress size={64} disableShrink thickness={3} /> : <ClubList clubs={clubs} />}
        {clubs.length !== 0 && (
          <Pagination
            page={params.page ?? 1}
            count={numPages ?? 1}
            size={isNotMobile ? 'medium' : 'small'}
            onChange={handlePageChange}
            sx={{ my: 3 }}
          />
        )}
      </Container>
    </>
  )
}

export default Search
