import axios, { AxiosError, type AxiosResponse } from 'axios'
import { useSnackbar } from 'notistack'
import { useAuthHeader } from 'react-auth-kit'
import { useMutation } from 'react-query'

import { COMMON_MESSAGE, DELETE_LIKE_MESSAGE, POST_LIKE_MESSAGE, SETTING_MESSAGE } from 'src/data/messages'

import type { ClubId } from 'src/models/Club'
import type { DateProps } from 'src/models/date'

export interface PatchClubBody {
  semesterInfo?: DateProps
  searchable?: boolean
}
interface PatchMutationParams {
  clubId: ClubId
  data: PatchClubBody
}
interface PatchParams extends PatchMutationParams {
  token: string
}
const patchClub = async ({ clubId, token, data }: PatchParams) => {
  await axios.patch<null, AxiosResponse<null>, PatchClubBody>(`${process.env.REACT_APP_APIHOST}/club/${clubId}`, data, {
    headers: { Authorization: token }
  })
}
export const usePatchClub = () => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()

  const mutateFn = ({ clubId, data }: PatchMutationParams) => patchClub({ clubId, token, data })

  return useMutation(mutateFn, {
    onSuccess: () => {
      enqueueSnackbar(SETTING_MESSAGE.SUCCESS, { variant: 'success' })
    },
    onError: error => {
      if (error instanceof AxiosError) {
        const errorMessage = error.response?.data?.message || SETTING_MESSAGE.FAIL
        enqueueSnackbar(errorMessage, { variant: 'error' })
      } else {
        enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
      }
    }
  })
}

export interface LikeParams {
  clubId: ClubId
  token: string
}

const postUserLikeInfo =
  ({ clubId, token }: LikeParams) =>
  async () => {
    await axios.post<null, AxiosResponse<null>, null>(
      `${process.env.REACT_APP_APIHOST}/user/like/club/${clubId}/`,
      null,
      {
        headers: { Authorization: token }
      }
    )
  }
export const usePostLikeClub = (clubId: ClubId) => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()

  return useMutation(postUserLikeInfo({ clubId, token }), {
    onSuccess: () => {
      enqueueSnackbar(POST_LIKE_MESSAGE.SUCCESS, { variant: 'success' })
    },
    onError: error => {
      if (error instanceof AxiosError) {
        if (error.response?.data?.statusCode == 401) {
          enqueueSnackbar(POST_LIKE_MESSAGE.LOGIN_WARNING, { variant: 'warning' })
        } else {
          const errorMessage = error.response?.data?.message || COMMON_MESSAGE.ADD_FAIL
          enqueueSnackbar(errorMessage, { variant: 'error' })
        }
      } else {
        enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
      }
    }
  })
}
const deleteUserLikeInfo =
  ({ clubId, token }: LikeParams) =>
  async () => {
    await axios.delete<null, AxiosResponse<null>, null>(`${process.env.REACT_APP_APIHOST}/user/like/club/${clubId}`, {
      headers: { Authorization: token }
    })
  }
export const useDeleteLikeClub = (clubId: ClubId) => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()

  return useMutation(deleteUserLikeInfo({ clubId, token }), {
    onSuccess: () => {
      enqueueSnackbar(DELETE_LIKE_MESSAGE.SUCCESS, { variant: 'success' })
    },
    onError: error => {
      if (error instanceof AxiosError) {
        const errorMessage = error.response?.data?.message || COMMON_MESSAGE.DELETE_FAIL
        enqueueSnackbar(errorMessage, { variant: 'error' })
      } else {
        enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
      }
    }
  })
}
